<template>
    <div class="container">
      <div class="box fx fx-ac">
        <span class="char iconfont icon-return" @click="$router.go('-1')"></span>
      </div>
      <h3 class="tx-c">修改密码</h3>
      <div class="pd-10">
        <mt-field label="手机号"  placeholder="请输入手机号" type="tel" v-model="phone"></mt-field>
        <mt-field label="验证码" placeholder="验证码" v-model="captcha2">
        <span class="get-code" @click="showCode">{{canClick?'获取验证码' :`还剩${count}s`}}</span>
        </mt-field>
        <mt-field label="新密码" placeholder="请输入新密码(8-16位字母或数字)" type="password" v-model="password"></mt-field>
        <mt-field label="确认密码" placeholder="请再次输入密码" type="password" v-model="password1"></mt-field>
        <mt-button size="large" type="primary" @click="sureBtn">确定</mt-button>
      </div>
      <div class="mask fx fx-ac fx-jc" v-if="mask">
        <div class="code-alert">
          <div class="top">
            <span>请先完成下方的验证</span>
            <span class="fz-20 iconfont icon-shanchudelete30" @click="closeMask"></span>
          </div>
          <div class="code">
            <div class="content fx fx-ac">
              <input class="input" type="text" maxlength="4" v-model="captcha1">
              <img :src="imgCode" width="60" height="30">
              <img @click="changeCode" src="../../assets/images/refresh.png" width="25" height="20">
            </div>
            <div class="mistake">{{msg}}</div>
            <mt-button @click="sure" class="btn" size="large" type="primary">确定</mt-button>
          </div>
       </div>
    </div>
  </div>
</template>

<script>
import { Toast } from 'mint-ui'
import { checkMobile, checkPwd } from '@/assets/js/util'
import { randUrl } from '@/assets/js/config'
export default {
  data () {
    return {
      msg: '',
      mask: false,
      canClick: true,
      imgCode: randUrl,
      phone: '',
      captcha1: '',
      captcha2: '',
      password: '',
      password1: '',
      count: 0
    }
  },
  created () {
  },
  methods: {
    sure () {
      if (this.captcha1 === '') {
        this.msg = '验证码不能为空'
        return
      }
      this.$api.requestchkCode({randCode: this.captcha1}).then(res => {
        if (!res.success) {
          this.msg = res.msg
          return
        }
        this.$api.requestSend({mobile: this.phone, flag: 3}).then(res => {
          console.log(res)
          if (res.success) {
            Toast({
              message: '验证码已发送',
              position: 'bottom'
            })
            this.canClick = false
            this.closeMask()
            this.count = 60
            let timer = setInterval(() => {
              this.count--
              if (this.count === 0) {
                this.canClick = true
                clearInterval(timer)
              }
            }, 1000)
          }
        })
      })
    },
    sureBtn () {
      if (this.phone === '' || this.password === '') {
        Toast('电话和密码不能为空')
        return
      }
      if (!checkPwd(this.password)) {
        Toast('密码格式不对')
        this.password = ''
        this.password1 = ''
        this.captcha2 = ''
        return
      }
      if (this.password !== this.password1) {
        Toast('两次输入密码不一致')
        return
      }
      this.$api.requestuPwd({name: this.phone, pwd: this.password, code: this.captcha2}).then(res => {
        if (res.success) {
          Toast('修改密码成功')
          this.$router.go(-1)
        } else {
          Toast(res.msg)
        }
      })
    },
    showCode () {
      if (this.phone === '' || !checkMobile(this.phone)) {
        Toast('手机号有误')
        return
      }
      if (!this.canClick) {
        return
      }
      this.mask = true
    },
    closeMask () {
      this.mask = false
      this.captcha1 = ''
      this.msg = ''
    },
    // 改变图形验证码
    changeCode () {
      this.imgCode = randUrl + '?' + new Date().getTime()
    }
  }
}
</script>

<style scoped lang="scss">
@import '@/assets/css/color.scss';
.container {
  z-index: 100;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  .box{
    height: 40px;
    .char{
      display: inline-block;
      width: 30px;
      height: 30px;
      margin-left: 10px;
      text-align: center;
      font-size: 25px;
    }
  }
  .get-code{
    color: $themeColor;
  }
}
.mask{
  z-index: 1;
  position: absolute;
  left:0;top:0;
  width: 100%;
  height: 100%;
  background: $opacity;
  .code-alert{
    width: 225px;
    height: 170px;
    background: #fff;
    .top{
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 29px;
      padding:0 20px;
      background: #e0e1e5;
      &:first-child{
        font-size: 12px;
      }
    }
    .code{
      margin-top: 30px;
      padding: 0 20px;
      .content{
        height: 30px;
        justify-content: space-between;
        .input{
          display: block;
          width: 74px;
          padding: 5px;
          font-size: 14px;
          border: 1px solid #e0e1e5;
          outline: 0;
        }
      }
      .mistake{
        padding: 10px;
        color: #e00;
        font-size:12px;
      }
      .btn{
        font-size: 16px;
      }
    }
  }
}
</style>

<style lang="scss" scoped>

</style>



